.InfoDiv {
    text-align: center;
    margin-top: 5%;
    border-color: black;
    border-width: 2px;
    border-style: solid;
    margin-left: 23%;
    margin-right: 23%;
    padding-bottom: 30px;
}

.GenButtons {
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    background-color: rgba(191, 191, 191, 1);
}

.GenDisplayText {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

@media(min-width: 500px){
    .InfoDiv {
        text-align: center;
        margin-top: 5%;
        border-color: black;
        border-width: 2px;
        border-style: solid;
        margin-left: 31%;
        margin-right: 31%;
        padding-bottom: 30px;
    }
    
    .GenButtons {
        padding-left: 10px;
        padding-right: 10px;
        margin-left: 5px;
        margin-right: 5px;
        background-color: rgba(191, 191, 191, 1);
    }
    
    .GenDisplayText {
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}